.index-title{
    color: #650300;
    font-size: 48px;
    line-height: 72px;
    font-weight: 700;
    text-align: center;
    margin: 0 0 48px 0;
}
.p-tp-80{
    padding: $m-8*10 0;
}

.banner{
    color: $FC-indexBanner;
    background: url(../images/home-header.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    p{
        font-size: 36px;
        line-height: 54px;
    }
    h2{
        font-size: 64px;
        line-height: 96px;
        font-weight: 700;
        margin: 0 0 4px 0;
    }
    a{
        font-size: 20px;
        line-height: 30px;
        font-weight: 500;
        padding: 6px 12px;
    }
    .banner-content{
        display: flex;
        flex-direction: column;
        align-items: end;
        padding: 296px 0;
    }
}



.bg-style{
    background: #fbf2f2;
}
.serve h3{
    margin: 0 0 24px 0;
}
.serve h4{
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    text-align: center;
    color: #650300;
    margin: 0 0 8px 0;
}
.serve ul li{
    max-width: 306px;
}
.serve img{
    max-width: 100px;
    margin: 16px auto;
    display: block;
}

.classic p, .design p{
    font-size: 48px;
    line-height: 72px;
    font-weight: 900;
    font-style: italic;
    color: #aa0601;
    text-align: center;
}
.classic p{
    margin: 8px 0 0 0;
}
.classic ul li{
    max-width: 416px;
}
.design .index-title{
    color: #fff;
}
.design p{
    padding: 8px 0;
    background: #fff;
}

.design{
    background: url(../images/index-bg.png);
}

.recommedation ul, .recommedation ul li{
    display: flex;
    justify-content: space-between;
}
.recommedation ul li{
    max-width: 306px;
    flex-direction: column;
    box-shadow: 0px 2px 6px #00000029;
}
.recommedation img{
    margin: 0 0 16px 0;
}
.recommedation p{
    margin: 0 0 8px 0;
    padding: 0 16px;
}
.recommedation-item{
    margin: 0 0 72px 0;
}
.date {
    font-size: 14px;
    line-height: 21px;
    color: #707070;
    margin: 0 0 16px 0;
}

.contact-form{
    max-width: 636px;    
}
.contact-form p{
    font-size: 20px;
    line-height: 30px;
    margin: 0 0 24px 0;
}
.contact-form label{
    display: block;
    margin: 0 0 8px 0;
}
.contact-form input{
    width: 636px;    
    padding: 6px 12px;
    margin: 0 0 16px 0;
}
.contact-form .suggestion{
    padding: 6px 12px 106px 12px;
}
.privacy-policy{
    display: flex;
    margin: 0 0 48px 0;
}
.privacy-policy input{
    display: block;
    width: 18px;
    height: 18px;
    margin: 4px;
}
.contact-form .submit{
    width: auto;
    font-weight: 700;
    padding: 16px 176px;
    margin: 0 110px;
}
